import React from "react";
import "../styles/Projects.css";
import FadeInSection from "./FadeInSection";
import FolderOpenRoundedIcon from "@material-ui/icons/FolderOpenRounded";
import GitHubIcon from "@material-ui/icons/GitHub";
import OpenInBrowserIcon from "@material-ui/icons/OpenInBrowser";

class Projects extends React.Component {
  constructor() {
    super();
    this.state = {
      expanded: true,
      activeKey: "1"
    };
    this.handleSelect = this.handleSelect.bind(this);
  }
  handleSelect(eventKey) {
    this.setState({
      activeKey: eventKey
    });
  }
  render() {
    const projects = {
      "Deepfake Detection": {
        desc: "Built a deep learning model with 98.33% accuracy using PyTorch and OpenCV to detect manipulated video content.",
        techStack: "Python, PyTorch, OpenCV, Deep Learning",
        link: "https://github.com/deployerarm",
        open: "https://github.com/deployerarm"
      },
      "Snakes & Ladders": {
        desc: "Developed a computational efficient 2-player console game in C language.",
        techStack: "C, Algorithm Design",
        link: "https://github.com/deployerarm",
        open: "https://github.com/deployerarm"
      },
      "PowerApps Texting App": {
        desc: "Created a low-code texting solution using Microsoft PowerApps for enterprise communication.",
        techStack: "Microsoft PowerApps, Low-code",
        link: "https://github.com/deployerarm",
        open: "https://github.com/deployerarm"
      }
    };

    return (
      <div id="projects">
        <div className="section-header">
          <span className="section-title">/ software-creations</span>
        </div>
        <div className="project-container">
          <ul className="projects-grid">
            {Object.keys(projects).map((key, i) => (
              <FadeInSection delay={`${i + 1}00ms`} key={i}>
                <li className="projects-card">
                  <div className="card-header">
                    <div className="folder-icon">
                      <FolderOpenRoundedIcon style={{ fontSize: 35 }} />
                    </div>
                    <div className="external-links">
                      <a className="github-icon" href={projects[key]["link"]}>
                        <GitHubIcon style={{ fontSize: 20, color: "var(--accent)" }} />
                      </a>
                      {projects[key]["open"] && (
                        <a className="open-icon" href={projects[key]["open"]}>
                          <OpenInBrowserIcon style={{ fontSize: 25, color: "var(--accent)" }} />
                        </a>
                      )}
                    </div>
                  </div>

                  <div className="card-title">{key}</div>
                  <div className="card-desc">{projects[key]["desc"]}</div>
                  <div className="card-tech">{projects[key]["techStack"]}</div>
                </li>
              </FadeInSection>
            ))}
          </ul>
        </div>
      </div>
    );
  }
}

export default Projects;